/* Footer Formatting */

:root {
  --mainColor: #2b2b2b;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

.footer-text {
  margin: 5px;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

.footer-text a {
  background: linear-gradient(
		to bottom,
		#2b2b2b 0%,
		#2b2b2b 100%
	);
  background: linear-gradient(
		to bottom,
		var(--mainColor) 0%,
		var(--mainColor) 100%
	);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 2px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  transition: background-size 0.4s;
}

.footer-text a:hover {
  background-size: 4px 50px;
}

.footer-text a:nth-child(2) {
  color: inherit;
  text-decoration: none; /* no underline */
  color: white;
  border-bottom: 0;
  background: none;
}

.footer-text img {
  max-width: 18px;
  transition: all 0.5s;
  vertical-align: middle;
}

.footer-text img:hover {
  transform: scale(0.8);
}
